import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';

import './SoldProducts.css';


import { toast } from 'react-toastify';
import { push } from 'react-router-redux';

import SideDrawer from '../AdminPage/components/SideDrawer';
import { store } from '../../store';
import agent from '../../agent';
import {
  GET_SOLD_PRODUCTS,
} from '../../constants/actionTypes';
import { USER_DETAILS } from '../../constants/otherConstants';
import Tabs from '../../common/Components/Tabs';
import TextInput from '../../common/Components/TextInput';
import Loader from '../../common/Components/Loader';
import CollapseTransition from '../../common/Components/CollapseTransition';


import DisabledDiv from '../../common/Components/DisabledDiv';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      user: {},
      isNormalUser: true,
      error: '',
    };
  }

  componentWillMount() {
    // console.log(this.props);
    if (this.props.user && this.props.user.user) {
      const temp = this.props.user.user.user_type === 'normal';
      this.setState({ user: this.props.user.user, isNormalUser: temp });
    }
    this.props.getSoldProducts()
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        const temp = np.user.user.user_type === 'normal';
        this.setState({ user: np.user.user, loading: false, isNormalUser: temp });
      }
    }
    if(np.soldProducts){
        console.log(np.soldProducts)
    }
  }

  addUserAccountDetails(e) {
    if (e) {
      e.preventDefault();
    }
    console.log(this.state.accountDetails);
  }

  render() {
    const { } = this.state.user;
    const {
      loading, mobileView, isNormalUser, accountDetails, visibleIndex, error,
    } = this.state;
    return (
      <div>
        <SideDrawer user={this.state.user} />

        <div className="admin-bg nav-margin dyna-flex  ">

        Sold products

        </div>
        {
          loading ? <Loader /> : null
        }

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  soldProducts: state.SoldProductsReducers.soldProducts
});

const mapDispatchToProps = (dispatch) => ({
  getSoldProducts: () => dispatch({ type: GET_SOLD_PRODUCTS, payload: agent.SoldProducts.getSoldProducts() }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
